<template>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container-fluid">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-collapse">
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand" href="#">
          <img src="@/../public/img/logo.png" />
        </a>
      </div>
      <div class="collapse navbar-collapse">
        <ul class="nav navbar-nav navbar-left">
          <li><router-link to="/index"><i class="glyphicon glyphicon-home"></i>首页</router-link></li>
          <li><router-link to="/movieList"><i class="glyphicon glyphicon-film"></i>电影</router-link></li>
          <li><router-link to="/news"><i class="glyphicon glyphicon-globe"></i>新闻资讯</router-link></li>
          
        </ul>
        <form class="navbar-form hidden-sm navbar-left" role="search">
          <div class="input-group navbar-form">
            <input v-model.lazy="search" type="text" class="form-control" placeholder="请输入电影名称">
            <span class="input-group-btn">
              <button @click="onSearch()" type="button" class="btn btn-danger">
                <i class="glyphicon glyphicon-search"></i>
              </button>
            </span>
          </div>
        </form>
        <ul class="nav navbar-nav navbar-right">
          <li v-if="isLogin ? true:false">
              <a @click="exit()" target="new" class="btn hidden-sm">
                <i class="glyphicon glyphicon-off"></i>退出
              </a>
          </li>
          <li v-else>
              <router-link to="/login" target="new" class="btn hidden-sm">
                <i class="glyphicon glyphicon-user"></i>登录/注册
              </router-link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
export default {
  data(){
    return{
      username:"",
      password:"",
      login:"",
      search:'',
      isLogin:sessionStorage.getItem("Authorization"),
    }
  },
  
  methods:{
    exit(){
      sessionStorage.removeItem("Authorization");
      this.$router.push('/index');
    },
    onSearch() {
      if (this.search.trim() == "") {
        alert('请输入搜索内容');
        return false;
      }else{
        this.$router.push({ name: 'search', params: { msg: '.'+this.search } });
        this.search = "";
      }
    },
  },
  watch:{
  },
}
</script>

<style scoped>
.navbar-brand{
  padding: 10px;
}
.navbar-brand img{
  width: 200px;
}
</style>