import {createRouter,createWebHistory} from "vue-router";
const router = createRouter({
  history:createWebHistory(),
  //linkActiveClass:'router-active',//自定义路由高亮类名，原来是：router-link-active
  routes:[
    {
      path:"/",
      redirect:'/index',//路由重定向
    },
    {
      path:"/index",//输出组件中声明的路由链接
      component:()=>import('@/components/index'),//延迟加载路由
      meta:{
        title:"首页",
      }
    },
    {
      path:"/search/:msg",
      component:()=>import('@/components/search'),
      name:'search',
      props: true,
      meta:{
        title:"电影搜索",
      }
    },
    {
      path:"/movieList",
      component:()=>import('@/components//movieList'),
      meta:{
        title:"电影",
      }
    },
    {
      path: '/prod-api/api/movie/film/detail/:id',
      name: 'details',
      component: () => import('@/components/movie/detail'),
      meta: {
        title: "电影详情",
      }
    },
    {
      path:"/news",
      component:()=>import('@/components/news'),
      meta:{
        title:"新闻资讯",
      }
    },
    {
      path:"/newsdetail/:id",
      name:'newsdetail',
      props: true,
      component:()=>import('@/components/news/newsDetail'),
      meta:{
        title:"新闻详情",
      }
    },
    {
      path:"/login",
      component:()=>import('@/components/login'),
      meta:{
        title:"登录",
      }
    },
    {
      path:"/register",
      component:()=>import('@/components/register'),
      meta:{
        title:"注册",
      }
    },
    {
      path:"/login",
      component:()=>import('@/components/login'),
      meta:{
        title:"登录",
      }
    },
  ]
});
//全局导航守卫
router.beforeEach((to,from)=>{

  document.title=to.meta.title;//修改页面标题
});
export default router;
